.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* CSS global pour forcer le footer en bas */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}

.page-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #f1f1f1; /* Couleur de fond du footer */
  padding: 10px 0;
  text-align: center;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
}
/* Fixer les informations utilisateur en bas de la sidebar */
.sidenav-main {
  position: fixed;
  /* height: 100vh; */
}

.user-info {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  background-color: #222; /* Fond sombre pour mieux contraster */
  color: white;
  text-align: center;
  /* z-index: 10;  */
}

/* Style du bouton de déconnexion */
.logout-btn {
  margin-top: 10px;
  background-color: red;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.logout-btn:hover {
  background-color: darkred;
}
