/* Conteneur principal */
.suivi-dossier-container {
    text-align: center;
    margin: 50px auto;
    padding: 40px;
    max-width: 800px;
    border-radius: 15px;
    background: #f9f9f9;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.candidate-info {
    margin-bottom: 30px;
    font-size: 18px;
    color: #424242;
    text-align: left;
    padding: 10px;
    background: #fafafa;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.candidate-info p {
    margin: 10px 0;
}

.progress-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0;
}

.step-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 100%;
}

.step-icon {
    width: 60px;
    height: 60px;
    font-size: 30px;
    margin-bottom: 10px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #e0e0e0;
    color: #757575;
    transition: background-color 0.3s, color 0.3s;
}

/* Couleur spécifique pour chaque étape */

/* Soumission des informations - Bleu */
.step-icon.soumission.completed {
    background-color: #2196F3; /* Bleu */
    color: white;
}

.step-icon.soumission.current {
    background-color: #2196F3; /* Bleu */
    color: white;
}

/* Paiement - Bleu clair */
.step-icon.paiement.completed {
    background-color: #dbce19; /* Bleu clair */
    color: white;
}

.step-icon.paiement.current {
    background-color: #dbce19; /* Bleu clair */
    color: white;
}

/* Traitement - Rouge */
.step-icon.traitement.completed {
    background-color: #F44336; /* Rouge */
    color: white;
}

.step-icon.traitement.current {
    background-color: #F44336; /* Rouge */
    color: white;
}

/* Résultat Provisoire - Jaune */
.step-icon.provisoire.completed {
    background-color: #4dcd16; /* Vert */
    color: white;
}

.step-icon.provisoire.current {
    background-color: #4dcd16; /* Vert */
    color: white;
}

/* Résultat Définitif - Vert */
.step-icon.definitif.completed {
    background-color: #4CAF50; /* Vert */
    color: white;
}

.step-icon.definitif.current {
    background-color: #4CAF50; /* Vert */
    color: white;
}

/* Étape en attente */
.step-icon.pending {
    background-color: #e0e0e0;
    color: #757575;
}

/* Titres des étapes */
.step-title {
    margin-top: 15px;
    font-size: 18px;
    color: #424242;
}

/* Titres avec les couleurs spécifiques */

/* Soumission des informations - Bleu */
.step-title.soumission.completed {
    color: #2196F3; /* Bleu */
}

.step-title.soumission.current {
    color: #2196F3; /* Bleu */
}

/* Paiement - Bleu clair */
.step-title.paiement.completed {
    color: #dbce19; /* Bleu clair */
}

.step-title.paiement.current {
    color: #dbce19; /* Bleu clair */
}

/* Traitement - Rouge */
.step-title.traitement.completed {
    color: #F44336; /* Rouge */
}

.step-title.traitement.current {
    color: #F44336; /* Rouge */
}

/* Résultat Provisoire - Jaune */
.step-title.provisoire.completed {
    color: #4dcd16; /* Jaune */
}

.step-title.provisoire.current {
    color: #4dcd16; /* Jaune */
}

/* Résultat Définitif - Vert */
.step-title.definitif.completed {
    color: #4CAF50; /* Vert */
}

.step-title.definitif.current {
    color: #4CAF50; /* Vert */
}

/* Barres de progression */
.progress-bar {
    height: 6px;
    background-color: #E0E0E0;
    flex: 1;
    margin-top: 10px;
}

.progress-bar.completed {
    background-color: #4CAF50;
}

/* Message de statut */
.status-message {
    margin-top: 30px;
    font-size: 18px;
    font-weight: bold;
    color: #424242;
}

.status-message.provisional {
    color: #FFC107; /* Jaune */
    font-weight: bold;
    background-color: #FFF9C4; /* Jaune clair pour le fond */
    padding: 15px;
    border-radius: 8px;
}
