.resultat-container {
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .candidate-info {
    margin-bottom: 20px;
  }
  
  .candidate-info p {
    font-size: 16px;
    color: #424242;
  }
  
  .status-message {
    margin: 20px 0;
    padding: 20px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: bold;
  }
  
  .status-message.success {
    background-color: #4caf50;
    color: white;
  }
  
  .status-message.error {
    background-color: #f44336;
    color: white;
  }
  
  .actions {
    margin-top: 20px;
  }
  
  button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .btn-primary {
    background-color: #4caf50;
    color: white;
  }
  
  .btn-secondary {
    background-color: #f44336;
    color: white;
  }
  
  button:hover {
    opacity: 0.8;
  }
  .status-message.unavailable {
    background-color: #f8d7da; /* Rouge clair */
    color: #721c24; /* Texte rouge foncé */
    padding: 15px;
    border: 1px solid #f5c6cb;
    border-radius: 5px;
    text-align: center;
    margin-top: 20px;
}

.status-message.unavailable h3 {
    margin: 0;
    font-size: 18px;
}

.status-message.unavailable p {
    font-size: 14px;
    margin-top: 10px;
}
/* Resultat.css */

.status-message.provisional {
    background-color: #fff4cc; /* Jaune clair */
    border: 1px solid #ffd700; /* Doré */
    padding: 15px;
    border-radius: 5px;
    text-align: center;
    margin-top: 20px;
}

.status-message.provisional h3 {
    color: #ffbf00; /* Texte jaune vif */
}

.status-message.provisional p {
    color: #a67c00; /* Texte jaune plus foncé */
}

